<template>
    <yd-layout>
        <yd-navbar title="应用中心" bgcolor="#70b6ef" color="#fff" fontsize="0.36rem">
            <router-link to="/" slot="left">
                <yd-navbar-back-icon color="#fff">
                    <slot color="#fff">返回</slot>
                </yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <div class="applicationCenter_box">
            <div class="mainContentFluid">
                <div class="fluidWidth">
                    <div class="color1"></div>
                    <div class="edit" v-if="editflag" @click="editfun">
                        编辑
                    </div>
                    <div class="complete" v-else @click="completefun">
                        完成
                    </div>
                    <yd-grids-group :rows="4" title="常用应用">
                        <yd-grids-item :class="deleteIcon" v-for="(value, index) in commonApp" @click.native.prevent="deleteApply(commonApp, index, value.URL)" :key="index">
                            <img slot="icon" :src="conf_url+value.IMG">
                            <span slot="text" v-text="value.CHNLNAME"></span>
                            <i slot="else" class="icon">—</i>
                        </yd-grids-item>
                    </yd-grids-group>
                </div>
            </div>
            <div class="mainContentFluid">
                <div class="fluidWidth">
                    <div class="color1"></div>
                    <yd-grids-group :rows="4" title="推荐应用">
                        <yd-grids-item :class="addIcon" v-for="(value, index) in recommendApp" @click.native.prevent="addRecommend(recommendApp[index], value.URL)" :key="index">
                            <img slot="icon" :src="conf_url+value.IMG">
                            <span slot="text" v-text="value.CHNLNAME"></span>
                            <i slot="else" class="icon">+</i>
                        </yd-grids-item>
                    </yd-grids-group>
                </div>
            </div>
            <div class="mainContentFluid" v-for="(id, index) in appCenter" :key="index">
                <div class="fluidWidth">
                    <div class="color2"></div>
                    <yd-grids-group :rows="4" :title="id.CHNLNAME">
                        <yd-grids-item :class="addIcon" v-for="(value, index) in id.secMenu" @click.native.prevent="addApply(id.secMenu[index], value.URL)" :key="index">
                            <img slot="icon" :src="conf_url+value.IMG">
                            <span slot="text" v-text="value.CHNLNAME"></span>
                            <i slot="else" class="icon">+</i>
                        </yd-grids-item>
                    </yd-grids-group>
                </div>
            </div>
        </div>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'applicationCenter',
    data() {
        return {
            editflag: true,
            deleteIcon: '',
            addIcon: '',
            conf_url: '',
            appCenter: [],
            commonApp: [],
            recommendApp: [],
            allData: [],
            userId: '',
            channelId: '',
            endFlag: '',
            commonDataCode: []
        }
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    mounted: function () {
        // 8月16号上线
        let vm = this;
        document.setTitle('应用中心');
        vm.conf_url = vm.conf.picURL;
        vm.initApplication();
    },
    methods: {
        editfun() {
            let vm = this;
            vm.deleteIcon = 'deteleactive';
            vm.addIcon = 'addactive';
            vm.editflag = false;
        },
        completefun() {
            let vm = this;
            vm.deleteIcon = '';
            vm.addIcon = '';
            vm.editflag = true;
            vm.userId = vm.getUserInfo.userid;
            vm.endFlag = ',';
            for (var i = 0; i < this.allData[0].length; i++) {
                if (i == this.allData[0].length - 1) {
                    vm.endFlag = '';
                }
                vm.channelId += this.allData[0][i].CHNLCODE + vm.endFlag;
            }
            vm.post(vm.conf.appliCenter + '/API/PortalMobile/menus/update/' + vm.channelId, {
                'name': vm.getUserInfo.ssotoken
            }, function (res) {})
        },
        initApplication() {
            let vm = this;
            vm.get(vm.conf.appliCenter + '/portal/appCenter', {
                'name': vm.getUserInfo.ssotoken
            }, function (res) {
                vm.commonApp = res.jsonData.commonAppList.jsonData;
                vm.recommendApp = res.jsonData.recommendAppList.jsonData;
                vm.appCenter = res.jsonData.appCenterList.data;
                vm.allData = [vm.commonApp, vm.recommendApp, vm.appCenter];
            })
        },
        deleteApply(commonApp, index, url) {
            let vm = this;
            if (vm.editflag == false) {
                commonApp.splice(index, 1);
            } else {
                vm.$router.push(url);
            }
        },
        addApply(data, url) {
            let vm = this;
            if (vm.editflag == false) {
                vm.commonDataCode = [];
                for (var i = 0; i < this.allData[0].length; i++) {
                    vm.commonDataCode.push(this.allData[0][i].CHNLCODE);
                }
                if (vm.commonDataCode.indexOf(data.CHNLCODE) >= 0 && vm.commonDataCode.length < 8) {
                    this.$dialog.toast({
                        mes: '该应用不能重复添加',
                        timeout: 1500
                    });
                }
                if (vm.commonDataCode.indexOf(data.CHNLCODE) == -1 && vm.commonDataCode.length < 8) {
                    this.allData[0].push(data);
                }
            } else {
                vm.$router.push(url);
            }
        },
        addRecommend(data, url) {
            let vm = this;
            if (vm.editflag == false) {
                var secCodeArr = [];
                var i = 0;
                var j = 0;
                var k = 0;
                for (i = 0; i < this.appCenter.length; i++) {
                    for (j = 0; j < this.appCenter[i].secMenu.length; j++) {
                        secCodeArr.push(this.appCenter[i].secMenu[j].CHNLCODE);
                    }
                }
                if (secCodeArr.indexOf(data.CHNLCODE) == -1) {
                    this.$dialog.toast({
                        mes: '该应用无权限添加',
                        timeout: 1500
                    });
                } else if (vm.commonDataCode.indexOf(data.CHNLCODE) >= 0 && vm.commonDataCode.length < 8) {
                    this.$dialog.toast({
                        mes: '该应用不能重复添加',
                        timeout: 1500
                    });
                } else {
                    vm.commonDataCode = [];
                    for (k = 0; k < this.allData[0].length; k++) {
                        vm.commonDataCode.push(this.allData[0][k].CHNLCODE);
                    }
                    if (vm.commonDataCode.indexOf(data.CHNLCODE) == -1 && vm.commonDataCode.length < 8) {
                        this.allData[0].push(data);
                    }
                }
            } else {
                vm.$router.push(url);
            }
        }
    }
}
</script>
<style scoped>
.fluidWidth {
    position: relative;
}

.mainContentFluid .deteleactive .yd-grids-icon {
    background: red;
}

.mainContentFluid {
    margin-top: 0;
    margin-bottom: .1rem;
}

.yd-grids-icon img {
    height: .48rem;
}

.icon {
    display: none;
    position: absolute;
    top: .1rem;
    width: .38rem;
    height: .38rem;
    right: .1rem;
    border-radius: 50%;
    color: #fff;
    text-align: center;
    font-size: .12rem;
    line-height: .35rem;
    background: #ff3232;
}

.yd-grids-item {
    position: relative;
    font-size: .24rem;
}

.edit,
.complete {
    position: absolute;
    right: 0.4rem;
    top: 0.27rem;
    font-size: 0.26rem;
    color: #999;
    z-index: 99;
}

.complete {
    color: #4dafff;
}

.fluidWidth {
    position: relative;
}

.yd-grids-txt {
    padding-bottom: .14rem;
}

.color1,
.color2,
.color3,
.color4,
.color5,
.color6 {
    position: absolute;
    left: 0;
    top: .27rem;
    background: #5ca6ff;
    width: .07rem;
    height: .27rem;
}

.color2 {
    background: #ffa93f;
}

.color3 {
    background: #ff583c;
}

.color4 {
    background: #ff5192;
}

.color5 {
    background: #58e17a;
}

.color6 {
    background: #ff4539;
}
</style>


